const header = document.querySelector('.header'); // 
const form = document.querySelector('.input__wrapper'); //  
const headerHome = document.querySelector('.home__btn'); //   Home
const headerLibrary = document.querySelector('.my-library__btn'); //   My library
const myLibraryBtnList = document.querySelector('.library-btn__list'); //   My library Watched  Queue
const watchedBtn = document.querySelector('.watched-btn');
const queueBtn = document.querySelector('.queue-btn');
const logo = document.querySelector('.logo__list');

headerHome.addEventListener('click', onheaderHomeBtnClick);
headerLibrary.addEventListener('click', onMyLibraryBtnClick);
watchedBtn.addEventListener('click', onWatchedBtnClick);
queueBtn.addEventListener('click', onQueueBtnClick);
logo.addEventListener('click', onheaderHomeBtnClick);

function onheaderHomeBtnClick() {
  myLibraryBtnList.classList.add('is-hidden'); //   Watched  Queue
  form.classList.remove('is-hidden'); // ' 
  headerHome.classList.add('current'); //      Home
  headerLibrary.classList.remove('current'); //     My library
  header.classList.remove('header__my-library'); //     My library
}

function onMyLibraryBtnClick() {
  form.classList.add('is-hidden'); //  
  myLibraryBtnList.classList.remove('is-hidden'); //'  Watched  Queue
  headerLibrary.classList.add('current'); //     My library
  headerHome.classList.remove('current'); //      Home
  header.classList.add('header__my-library'); //     
}

function onWatchedBtnClick() {
  queueBtn.classList.remove('active');
  watchedBtn.classList.add('active');
}

function onQueueBtnClick() {
  watchedBtn.classList.remove('active');
  queueBtn.classList.add('active');
}

// add-to-watched/watched-btn
export { onWatchedBtnClick, onQueueBtnClick };